/* General Navbar Styling */
.navbar {
  background-color: #1a202c;
  color: white;
  padding: 10px 30px;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navbar-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
}

/* Logo */
.logo {
  font-size: 1.8rem;
  font-weight: bold;
  color: #facc15;
  text-decoration: none;
}

/* Search Bar */
.navbar-search {
  display: flex;
  flex-grow: 1;
  max-width: 600px;
  margin: 0 20px;
}

.search-input {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  font-size: 14px;
}

.search-button {
  padding: 10px 20px;
  background-color: #facc15;
  border: none;
  border-radius: 0 4px 4px 0;
  color: black;
  font-weight: bold;
  cursor: pointer;
}

.search-button:hover {
  background-color: #ffd700;
}

/* Right Section */
.navbar-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.icon {
  font-size: 1.5rem;
  cursor: pointer;
  color: white;
  transition: color 0.3s ease;
}

.icon:hover {
  color: #facc15;
}

/* Language Dropdown */
.language-dropdown {
  position: relative;
}

.language-selector img {
  width: 24px;
  height: 16px;
  cursor: pointer;
}

.language-options {
  position: absolute;
  top: 40px;
  right: 0;
  background-color: white;
  color: black;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.language-option {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.language-option:hover {
  background-color: #facc15;
  color: black;
}

/* Account Dropdown */
.account-dropdown {
  position: relative;
}

.account-button {
  font-size: 1.5rem;
  cursor: pointer;
}

.account-menu {
  position: absolute;
  top: 40px;
  right: 0;
  background-color: white;
  color: black;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.account-link,
.logout-button {
  display: block;
  padding: 10px 15px;
  text-decoration: none;
  color: black;
  transition: background-color 0.3s ease;
  font-size: 14px;
}

.account-link:hover,
.logout-button:hover {
  background-color: #facc15;
  color: black;
}

.logout-button {
  border: none;
  background: none;
  cursor: pointer;
  text-align: left;
}


.icon {
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
}


.badge {
  position: absolute;
  top: -5px;
  right: -10px;
  background-color: red;
  color: white;
  font-size: 0.8rem;
  border-radius: 50%;
  padding: 3px;
}

@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }
}
