.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 320px;
  height: 100%;
  background-color: #1a202c;
  color: #f0f0f0;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
  z-index: 1000;
  padding-top: 20px;
  overflow-y: auto;
  transform: translateX(-100%);
  font-family: 'Arial', sans-serif;
}

.sidebar.open {
  transform: translateX(0);
}

.close-button {
  background: none;
  border: none;
  font-size: 24px;
  color: #f0f0f0;
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 15px;
}

.sidebar-user {
  display: flex;
  align-items: center;
  background-color: #2d3748;
  color: #fff;
  padding: 15px;
  font-weight: bold;
  border-bottom: 1px solid #4a5568;
}

.user-icon {
  font-size: 30px;
  margin-right: 10px;
}

.login-button {
  background-color: #3182ce;
  border: none;
  color: #fff;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
}

.login-button:hover {
  background-color: #2b6cb0;
}

.sidebar-section h3 {
  color: #63b3ed;
  margin-bottom: 10px;
  padding-left: 15px;
}

.sidebar-section ul {
  list-style: none;
  padding: 0 15px;
}

.category-header {
  padding: 10px 0;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #a0aec0;
}

.category-header:hover {
  color: #63b3ed;
}

.subcategory {
  padding-left: 20px;
}

.subcategory li {
  padding: 5px 0;
  color: #a0aec0;
}

.see-all-btn {
  background-color: #63b3ed;
  border: none;
  padding: 10px;
  margin: 10px 0;
  cursor: pointer;
  width: 100%;
  border-radius: 4px;
}

.logout-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1500;
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.language-buttons button {
  background: none;
  border: none;
  padding: 5px;
}

.city-selection-modal {
  background: #fff;
  padding: 20px;
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #ddd;
  margin-top: 10px;
}

.city-option {
  padding: 8px 10px;
  margin: 5px;
  border: 1px solid #ddd;
  cursor: pointer;
}

.city-option:hover {
  background-color: #f0f0f0;
}

.cancel-button {
  padding: 10px 15px;
  margin-top: 10px;
  background-color: #e74c3c;
  color: #fff;
  border: none;
  cursor: pointer;
}

.cancel-button:hover {
  background-color: #c0392b;
}

.city-selection-button {
  padding: 8px 12px;
  background-color: #3498db;
  color: #fff;
  border: none;
  cursor: pointer;
}

.city-selection-button:hover {
  background-color: #2980b9;
}

.language-buttons button {
  background: none;
  border: none;
  padding: 5px;
}

.flag-icon {
  width: 20px;
  height: 15px;
  margin-right: 5px;
}

.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 999;
}

/* Responsive Design */
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
  }

  .close-button {
    font-size: 20px;
  }

  .sidebar-user {
    padding: 10px;
  }

  .sidebar-section {
    padding: 10px;
  }

  .sidebar-section ul li {
    font-size: 14px;
  }

  .subcategory li {
    font-size: 13px;
  }

  .see-all-btn {
    padding: 8px;
    font-size: 14px;
  }
}
