/* RTL Layout Adjustments */
.rtl {
  direction: rtl;
  text-align: right;
}
  
  .rtl .navbar {
    flex-direction: row-reverse;
  }
  
  .rtl .navbar-left, .rtl .navbar-right {
    flex-direction: row-reverse;
  }
  
  .rtl .navbar-search {
    flex-direction: row-reverse;
  }
  
  .rtl .auth-container {
    direction: rtl;
    text-align: right;
  }
  