.profile {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 8px;
}

.profile h2 {
  text-align: center;
  margin-bottom: 20px;
}

.profile form {
  margin-bottom: 30px;
}

.profile label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.profile input {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.profile button {
  display: block;
  width: 100%;
  padding: 10px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.profile button:hover {
  background: #0056b3;
}
