.favorite-list {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

.empty-message {
  text-align: center;
  color: #888;
  font-size: 18px;
}

.favorite-items-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.favorite-item {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
}

.favorite-item:hover {
  transform: scale(1.02);
}

.favorite-item-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 1px solid #ddd;
}

.favorite-item-details {
  padding: 15px;
  text-align: center;
}

.favorite-item-name {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

.favorite-item-price {
  font-size: 16px;
  color: #888;
  margin-bottom: 15px;
}

.favorite-item-actions {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.remove-from-favorite-btn,
.add-to-cart-btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.remove-from-favorite-btn:hover {
  background-color: #dc3545;
}

.add-to-cart-btn:hover {
  background-color: #0056b3;
}
