.seller-profile-container {
    padding: 20px;
    max-width: 1200px;
    margin: auto;
  }
  
  h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .summary-section {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
  }
  
  .summary-card {
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    text-align: center;
  }
  
  .products-section,
  .orders-section,
  .statistics-section {
    margin-bottom: 30px;
  }
  
  .products-table,
  .orders-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  .products-table th,
  .orders-table th,
  .products-table td,
  .orders-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
  }
  
  .products-table th,
  .orders-table th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  
  .add-product-button {
    margin-bottom: 10px;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-product-button:hover {
    background-color: #0056b3;
  }
  
  .statistics {
    display: flex;
    justify-content: space-around;
  }
  
  .stat {
    text-align: center;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    background-color: #f9f9f9;
  }
  