.signup-container {
  max-width: 500px;
  margin: 50px auto;
  padding: 30px;
  border-radius: 12px;
  background-color: #f7f7f7;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.signup-container h2 {
  margin-bottom: 20px;
  font-size: 1.8rem;
  color: #333;
  font-weight: bold;
}

.signup-container form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.signup-container input {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
}

.signup-container input:focus {
  border-color: #4caf50;
}

button {
  padding: 12px;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.next-button {
  background-color: #4caf50;
  color: #fff;
}

.next-button:hover {
  background-color: #45a049;
}

.create-account-button {
  padding: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.create-account-button:hover {
  background-color: #45a049;
}


.error-message {
  color: red;
  font-size: 0.9rem;
  font-weight: bold;
}

p {
  margin-top: 15px;
  font-size: 0.9rem;
  color: #555;
}

p a {
  color: #4caf50;
  font-weight: bold;
}

p a:hover {
  text-decoration: underline;
}

.step-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.previous-button {
  padding: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #ff6f61;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.previous-button:hover {
  background-color: #e65b50;
}

