
/* Vendor Page Styling */
.vendor-page {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.vendor-header {
  text-align: center;
  margin-bottom: 30px;
}

.vendor-header h1 {
  font-size: 28px;
  color: #333;
}

.vendor-header p {
  font-size: 16px;
  color: #555;
}

.vendor-details {
  margin-bottom: 40px;
}

.vendor-details h2 {
  font-size: 22px;
  color: #333;
  margin-bottom: 10px;
}

.product-listings h2 {
  font-size: 22px;
  color: #333;
  margin-bottom: 15px;
}

.sort-options {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.sort-options label {
  font-size: 14px;
  color: #333;
}

.products {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.product-item {
  text-align: center;
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-item img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 10px;
}

.product-item h3 {
  font-size: 18px;
  color: #333;
}

.product-item p {
  font-size: 16px;
  color: #555;
}

.product-item:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.error-message {
  color: red;
  text-align: center;
  margin: 10px 0;
}

.loading-message {
  text-align: center;
  font-style: italic;
  color: #666;
}

/* Responsive Enhancements */
@media (max-width: 768px) {
  .products {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .products {
    grid-template-columns: repeat(2, 1fr);
  }
}
