/* Footer Styling */
.footer {
  background-color: #232f3e;
  color: #fff;
  font-family: "Roboto", sans-serif;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Back to Top */
.footer-back-to-top {
  text-align: center;
  padding: 10px;
  background-color: #444;
  cursor: pointer;
  font-size: 14px;
  color: #f0c040;
  transition: background-color 0.3s ease;
}

.footer-back-to-top:hover {
  background-color: #555;
}

/* Footer Links Section */
.footer-links {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.footer-column {
  flex: 1;
}

.footer-column h3 {
  font-size: 18px;
  color: #f0c040;
  margin-bottom: 10px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
}

.footer-column li {
  font-size: 14px;
  margin-bottom: 8px;
  color: #ccc;
}

.footer-column a {
  color: #f0c040;
  text-decoration: none;
}

.footer-column a:hover {
  text-decoration: underline;
}

/* Footer Social Icons */
.footer-social {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.footer-social-icon {
  font-size: 20px;
  color: #f0c040;
  transition: color 0.3s ease;
}

.footer-social-icon:hover {
  color: #ff9900;
}

/* Footer Bottom Section */
.footer-bottom {
  text-align: center;
  font-size: 14px;
  border-top: 1px solid #444;
  padding-top: 10px;
  color: #ccc;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .footer-links {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-column {
    margin-bottom: 20px;
  }
}
