.cart-page {
  padding: 20px;
  max-width: 800px;
  margin: auto;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.cart-items {
  margin-bottom: 20px;
}

.cart-summary {
  padding: 20px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.cart-summary h2 {
  margin-bottom: 10px;
  color: #333;
}

.cart-summary p {
  margin: 5px 0;
}

.total {
  font-weight: bold;
  font-size: 1.2em;
  color: #000;
}

.checkout-button,
.empty-cart-button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.checkout-button {
  background-color: #28a745;
  color: white;
  transition: background-color 0.3s ease;
}

.checkout-button:hover {
  background-color: #218838;
}

.empty-cart-button {
  background-color: #dc3545;
  color: white;
  transition: background-color 0.3s ease;
}

.empty-cart-button:hover {
  background-color: #c82333;
}

.empty-cart {
  text-align: center;
}

.shop-now-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.shop-now-button:hover {
  background-color: #0056b3;
}
