.secondary-navbar {
  background-color: #232f3e;
  padding: 2px 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Roboto", sans-serif;
  position: sticky;
  top: 0;
  z-index: 999;
}

.secondary-navbar-content {
  display: flex;
  align-items: center;
  width: 100%;
}

/* Sidebar Toggle Button */
.toggle-button {
  background-color: #232f3e;
  color: #f0c040;
  padding: 8px 12px;
  border: none;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  margin-right: 20px; /* Space between toggle button and links */
}

.toggle-button i {
  margin-right: 5px;
}

.toggle-button:hover {
  background-color: #1e272e;
}

/* Links Section */
.secondary-navbar-links {
  display: flex;
  align-items: center;
  gap: 20px;
  flex-grow: 1; /* Push links to the right */
  justify-content: flex-start;
}

.secondary-navbar-link {
  color: #f0c040;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: color 0.3s ease;
  display: flex;
  align-items: center;
  gap: 5px;
}

.secondary-navbar-link i {
  font-size: 16px;
}

.secondary-navbar-link:hover {
  color: #f0c040;
}

/* Sell Button */
.sell-button {
  background: none;
  border: none;
  color: #f0c040;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 5px;
}

.sell-button:hover {
  color: #f0c040;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .secondary-navbar-links {
      gap: 10px;
  }
}

@media (max-width: 480px) {
  .secondary-navbar {
      flex-direction: column;
      align-items: flex-start;
  }
  .secondary-navbar-links {
      flex-wrap: wrap;
      justify-content: flex-start;
      gap: 10px;
  }
  .toggle-button {
      margin-bottom: 10px;
  }
}
